$prefix: '.rise-blocks';

div[class^="rise-blocks-social-align-"] {
	display: flex;

	#{$prefix}-social-icon-wrapper {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: 10px;

		a {
			display: flex;
			align-items: center;
			justify-content: center;
			text-decoration: none;
		}
	}

	.wp-block-rise-blocks-social-icon {
		&:last-child {
			#{$prefix}-social-icon-wrapper {
				margin-right: 0;
			}
		}
	}
}

#{$prefix}-social-align-center {
	justify-content: center;
}

#{$prefix}-social-align-right {
	justify-content: flex-end;
}

